import chunLeiModal from '@/components/chunLei-modal/chunLei-modal.vue';
import uniSection from '@/components/uni-section/uni-section.vue';
import uniFab from '@/components/uni-fab/uni-fab.vue';
import uniCard from '@/components/uni-card/uni-card.vue';
import uniTag from '@/components/uni-tag/uni-tag.vue';
import uniPagination from '@/components/uni-pagination/uni-pagination.vue';
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
export default {
	components: {
		uniCard,
		uniTag,
		uniFab,
		uniPagination,
		uniSection,
		chunLeiModal,
		uniList,
		uniListItem
	},
	data() {
		return {
			reminderCount:0,
			/*天气*/
			weather: {
				code: 0,
				realtime: {
					"temperature": "",
					"humidity": "",
					"info": "",
					"wid": "",
					"direct": "",
					"power": "",
					"aqi": ""
				},
				future: {
					"date": "",
					"temperature": "",
					"weather": "",
					"wid": {
						"day": "",
						"night": ""
					},
					"direct": ""
				}
			},
			location: '',
			/*模态框*/
			model_value: false,
			model_type: 'default',
			model_data: {},
			model_defaultData: {
				title: '注销',
				content: '是否退出?',
				cancelText: '取消',
				confirmColor: '#3CC51F'
			},
			/*抽屉*/
			showMyInfo: false,
			showVersion: false,
			/*悬浮按钮模块*/
			add_title: 'uni-fab',
			add_directionStr: '垂直',
			add_horizontal: 'left',
			add_vertical: 'bottom',
			add_direction: 'vertical',
			add_pattern: {
				color: '#7A7E83',
				backgroundColor: '#fff',
				selectedColor: '#007AFF',
				buttonColor: '#007AFF'
			},
			add_content: [{
					iconPath: '/static/component.png',
					selectedIconPath: '/static/images/newMsg.png',
					text: '写文章',
					active: true
				},
				{
					iconPath: '/static/api.png',
					selectedIconPath: '/static/images/user.png',
					text: '我的',
					active: true
				},
				{
					iconPath: '/static/template.png',
					selectedIconPath: '/static/images/version.png',
					text: '版本信息',
					active: true
				}
			],
			imageUrlsArr: [],
			msgArr: [],
			user: {
				username: "zjh",
				nickName: ''
			},
			JSESSIONID: '',
			//分页组件
			totalPage: ''
		}
	},
	onLoad(opt) {

		if (opt.jsd != undefined) {
			this.setJSESSIONID(opt.jsd)
			opt.jsd = '';
		}

		const that = this;
		setTimeout(function() {
			that.userStatus()
		}, 200)
		//判断是从登录页过来的
		//加载天气
		if(opt.login != undefined)
		this.getLocation()
		
	},
	onPullDownRefresh() {

		setTimeout(() => {
			this.userStatus();
			//关闭下拉刷新 
			
		}, 500)

	},
	methods: {
		toAboutMe(){
			uni.navigateTo({ 
				url: '../aboutMe/aboutMe?jsd='+this.JSESSIONID+'&count='+this.reminderCount
			});
		},
		loadingReminderCount(){
			const that = this;
			uni.request({
				url: 'http://zjhwork.xyz:8080/getReminderCount',
				method: 'GET',

				header: {
					"Cookie": "JSESSIONID=" + that.JSESSIONID
				},

				success: res => {
					that.reminderCount = res.data;
				}
			});
		},
		commentIt(msgId){
			const that = this;
			uni.navigateTo({
				url: '../msgDetail/msgDetail?jsd='+that.JSESSIONID+'&msgId='+msgId
			});
		},
		/*模态框*/
		cancel() {
			//点击取消函数
		},
		onConfirm(e) {
			const that = this;
			switch (this.model_type) {
				case 'default':
					that.userLogout()
					break;
			}
		},
		tapBtn(type) {

			this.model_type = type 
			this.model_value = !this.model_value
			switch (this.model_type) {
				case 'default':
					this.model_data = this.model_defaultData
					break;
			}
		},
		/*用户退出*/
		userLogout() {
			const that = this;
			uni.showLoading({
				title: "正在注销~"
			})
			uni.request({
				url: "http://zjhwork.xyz:8080/userLogOut",
				success: () => {
					//清空JSESSIONID号
					that.JSESSIONID = '';
					uni.hideLoading()
					that.userStatus()
				}
			})
		},
		closeDrawer(e) {
			if (e === 'left') {
				this.showMyInfo = false
			} else {
				this.showVersion = false
			}
		},
		clickAboutMeTag(){
			this.showVersion = true;
		},
		add_trigger(e) {
			/*监听浮动按钮*/
			if (e.index == 1) {
				this.showMyInfo = true;
			} else if (e.index == 2) {
				this.showVersion = true;
			} else {
				uni.navigateTo({
					url: '../details/details?jsd=' + this.JSESSIONID
				})
			}
		},
		//加载页码
		getTotallyPage() {
			const that = this;
			uni.request({
				url: "http://zjhwork.xyz:8080/msgCount",

				header: {
					"Cookie": "JSESSIONID=" + that.JSESSIONID
				},

				
				success: (res) => {
					that.totalPage = res.data;
				}
			})
		},
		changePage(e) {
			this.loadingMsg(e.current)
		},
		setJSESSIONID(jsd) {
			this.JSESSIONID = jsd;
		},
		pressImg(current) {
			let arr=[]
			arr.push(current)
			uni.previewImage({
				current:0,
				urls: arr,
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			})
		},
		userStatus() {
			uni.showLoading({
				title: "正在加载",
			})
			const that = this;
			uni.request({
				url: "http://zjhwork.xyz:8080/status",






				header: {
					"Cookie": "JSESSIONID=" + that.JSESSIONID
				},

				success: (res) => {
					if (res.data.code == 1) {
						that.user.username = res.data.result[0].username;
						
						that.loadingReminderCount()
						that.loadingMsg(1)
					} else {
						uni.hideLoading()
						that.toLogin()
					}
					uni.stopPullDownRefresh();
				}
			})
		},
		getLocation() {
			
			const that = this;
			//初始化定位和天气信息
			that.weather.code = 0;
			uni.getLocation({
				type: 'wgs84',
				success: (res) => {
					uni.request({
						url: 'http://api.map.baidu.com/geocoder/v2/?callback=renderReverse&location=' + res.latitude + ',' + res.longitude +
							'&output=json&pois=1&ak=lwLh9Z2DLncf1uqymwtQTuAIYWwISgP1',
						success: (weather) => {
							that.loadingWeather(JSON.parse(weather.data.substr(29, weather.data.length - 30)).result.addressComponent
								.city)
							that.location = JSON.parse(weather.data.substr(29, weather.data.length - 30)).result.formatted_address;
						}
					})
				}
			})
		},
		loadingWeather(city) {
			const that = this;
			uni.request({
				url: "http://apis.juhe.cn/simpleWeather/query?city=" + city.substr(0, city.length - 1) +
					"&key=93115b9570227d60a64ddabb4ad2123f",
				success: (res) => {
					
					if(res.data.result == null){
						uni.showToast({
							title:'今日天气api已经超过次数啦',
							icon:'none',
							duration:1000,
							position:'bottom'
						})
					}else{
						that.weather.code = 1;
						//实况天气
						that.weather.realtime = res.data.result.realtime;
						// //今日天气
						 that.weather.future = res.data.result.future[0];
					}
					
				}

			})


		},
		loadingMsg(currPage) {
			
			//加载页码
			this.getTotallyPage();
			const that = this;
			that.imageUrlsArr = [];
			//获取第一页信息
			uni.request({
				url: 'http://zjhwork.xyz:8080/loadingMsg?currPage=' + currPage,
				method: 'Get',
				dataType: 'json',






				header: {
					"Cookie": "JSESSIONID=" + that.JSESSIONID
				},

				success: (res) => {   
					for (var i = 0; i < res.data.result.length; i++) {
						//本地小表情
						res.data.result[i].content = res.data.result[i].content.replace(new RegExp('images/', 'g'),
							'http://zjhwork.xyz:8080/images/')
						//服务器图片
						if (res.data.result[i].imgUrl != undefined) {
							that.imageUrlsArr.push("http://zjhwork.xyz:8080/" + res.data.result[i].imgUrl)
							res.data.result[i].imgUrl = res.data.result[i].imgUrl.replace(new RegExp('/getImgById', 'g'),
								'http://zjhwork.xyz:8080/getImgById')
						} else {
							that.imageUrlsArr.push("http://zjhwork.xyz:8080/images/noPng.png");
						}
					}
					if (res.data.code == 1) {
						that.msgArr = res.data.result;
					} else {
						uni.showToast({
							title: '您还尚未登录，请登录',
							duration: 2000,
							position: 'bottom',
							icon: 'none'
						})
					}
					uni.hideLoading()
				}
			})
			//加载个人信息
			uni.request({
				url: 'http://zjhwork.xyz:8080/findNickNameByUsername?username=' + that.user.username,
				method: 'Get',
				header: {




					"Cookie": "JSESSIONID=" + that.JSESSIONID,

					//发送表单数据   否则默认请求是json数据
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				dataType: 'json',
				success: function(res) {
					that.user.nickName = res.data.result[0].username;
				}
			});

		},
		toLogin() {
			uni.showToast({
				title: "还未登陆哦！请先登录~",
				duration: 2000,
				icon: 'none'
			})
			//重定向到登陆页面
			setTimeout(function() {
				uni.redirectTo({
					url: "../login/login"
				})
			}, 1500)
		}
	}
}
